import React, { useState } from "react";
import Logo from "../assets/logo.png";
import ReorderIcon from "@material-ui/icons/Reorder";
import "../styles/Navbar.css";

function Navbar() {
  const [openLinks, setOpenLinks] = useState(false);

  const toggleNavbar = () => {
    setOpenLinks(!openLinks);
  };
  return (
    <div className="navbar">
      <div className="leftSide" id={openLinks ? "open" : "close"}>
        <img src={Logo} />
        <div className="hiddenLinks">
          <a href="http://quizoid-home.netlify.app">Home</a>
          <a href="http://quizoid.netlify.app/Categories">Categories</a>
          <a href="http://quizoid.netlify.app/ChooseLeaderBoard">Leaderboard</a>
          <a href="http://quizoid.netlify.app/login">Login</a>
        
        </div>
      </div>
      <div className="rightSide">
      <a href="http://quizoid-home.netlify.app">Home</a>
          <a href="http://quizoid.netlify.app/Categories">Categories</a>
          <a href="http://quizoid.netlify.app/ChooseLeaderBoard">Leaderboard</a>
          <a href="http://quizoid.netlify.app/login">Login</a>
        <button onClick={toggleNavbar}>
          <ReorderIcon />
        </button>
      </div>
    </div>
  );
}

export default Navbar;
